@mixin u-300 {
  @media screen and (max-width: 320px) {
    @content;
  }
}

@mixin header-collapse {
  @media screen and (max-width: 484px) {
    @content;
  }
}

.signup__form {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 40px;
  @include u-300 {
    padding: 20px;
  }
  & .oAuth {
    display: flex;
    align-items: center;
    justify-content: center;
    & h3 {
      cursor: default;
      padding: 0 20px;
      min-width: 140px;
      @include header-collapse {
        padding: 0;
      }
    }
    &__button {
      min-height: 40px;
      min-width: 40px;
      background-size: contain;
      margin: 0 5px;
      background-repeat: no-repeat;
      background-position: center;
      cursor: pointer;
      transform: none;
      &:hover {
        transition: transform 0.4s;
        animation: bounceUp 0.4s normal;
      }
      &--google {
        background-image: url("/Media/google-dark.svg");
      }
      &--microsoft {
        background-image: url("/Media/ms.svg");
        background-size: 37.5px;
      }
    }
  }
  & p {
    text-align: center;
    font-size: 0.9em;
    & a {
      color: #69be94;
      &:hover {
        color: #40576d;
      }
    }
  }
    &__item {
      border: 1px solid #40576d;
      position: relative;
      border-radius: 5px;
      height: 32px;
      display: flex;
      flex-direction: column-reverse;
      justify-content: flex-start;
      margin: 10px;
      padding: 5px;
      &--input {
        padding: 0px 6px;
        outline: none;
        border: none;
        height: 20px;
        &--contains {
          &+ label {
            cursor: default!important;
            top: 2px;
            left: 5;
            font-size: 0.7em;
            cursor: text;
          }
        }
      }
      &--label {
        cursor: text;
        position: absolute;
        top: 12px;
        left: 10px;
        transition: top 0.4s, left 0.4s, font-size 0.4s;
        color: #40576d;
      }
    }
    &__button {
      outline: none;
      border: 1px solid black;
      margin: 30px 10px 0 10px;
      cursor: pointer;
      height: 44px;
      border-radius: 5px;
      transition: background-color 0.4s;
      &:hover {
        transition: background-color 0.4s;
        background-color: #69be94;
      }
    }
  }
